@function em($size, $context: 16) {
	@return ($size/$context) * 1em; }

@function img-url($image) {
	@return url('../img/#{$image}'); }

@mixin image($image) {
	background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
	transition: $what $dur $easing; }

@mixin smooth-scroll {
	-webkit-overflow-scrolling: touch; }

@mixin smooth-font {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

@mixin gpu {
	transform: translate3d(0,0,0); }

@mixin lg {
	@include r(1200) {
		@content; } }

@mixin md {
	@include r(1080) {
		@content; } }

@mixin sm {
	@include r(767) {
		@content; } }

@mixin xs {
	@include r(599) {
		@content; } }


@mixin f($size: null, $line-height: null, $spacing: null) {
	@if $size != null {
		font-size: $size + px; }
	@if $line-height != null {
		line-height: (ceil(($line-height / $size)*100)/100); }
	@if $spacing != null {
		letter-spacing: ($spacing / $size) + em; } }

@mixin hover {
	.no-touch & {
		&:hover {
			@content; } } }
@mixin active {
	.no-touch & {
		&:active {
			@content; } }
	&:active {
		@content; } }

@mixin no-touch {
	.no-touch & {
		@content; }
	@content; }

@mixin t($what) {
	transition: $what 0.25s ease; }

//COLORS
$blue: #3160aa;
$blue-light: #9db7df;
$blue-dark: #294c84;
$blue-darken: #3b5998;

$text: #5d6a7e;

$dark: #1d2e49;

$gray: #e3e6ec;
$darkGray: #a7aeba;

$red: #e64e49;
