@include font("Bebas Regular", "bebasneue_regular");
@include font("Evogria Regular", "evogria");
@include font("Gilroy Light", "gilroy-light");
@include font("Gilroy Extrabold", "gilroy-extrabold");

@mixin bebas {
	font-family: "Bebas Regular", Arial, sans-serif;
	font-weight: normal; }

@mixin evogria {
	font-family: "Evogria Regular", Arial, sans-serif;
	font-weight: normal; }

@mixin light {
	font-family: "Gilroy Light", Arial, sans-serif;
	font-weight: normal; }

@mixin bold {
	font-family: "Gilroy Extrabold", Arial, sans-serif;
	font-weight: normal; }
